@import 'components/variables';
@import "components/generales/accordion";
@import 'components/generales/banner-dudas.scss';

.section-1 {
  padding-top: 3rem;
  padding-bottom: 3rem;
  li {
    color: $gray-siroco;
    span {
      font-weight: 800;
    }
  }
  .table {
    color: $white;
    overflow-y: hidden;
    @media (max-width: 424.98px) {
      font-size: .8rem;
    }
  }
  .tabla1 {
    background-color: $blue-tabla1;
    border-radius: 10px;
  }
  .tabla2 {
    background-color: $blue-tabla2;
  }
  .tabla3 {
    background-color: $blue-tabla3;
    border-radius: 10px;
  }
  .tabla4 {
    background-color: $blue-tabla4;
  }
  .tabla5 {
    background-color: $blue-tabla4;
    border-radius: 10px;
  }
  .tabla6 {
    background-color: $blue-tabla2;
    border-radius: 10px;
  }
  .line-material {
    width: 6.5rem;
    height: .6rem;
    background-color: $blue-tabla4;
    opacity: 1;
  }
  .contenido-card {
    padding: 2rem;
    background-color: $blue-claro;
  }
  .contenido-card2 {
    padding: 2rem;
    background-color: #6EC6EB;
    transform: translateY(30px);
    @media (max-width: 767.98px) {
      transform: translateY(0);
      margin-top: 1rem;
    }
  }
  .contenido-card3 {
    padding: 2rem;
    background-color: #6EC6EB;
  }
  .contenido-card4 {
    padding: 2rem;
    background-color: $blue-claro;
    transform: translateY(30px);
    @media (max-width: 767.98px) {
      transform: translateY(0);
      margin-top: 1rem;
    }
  }
  .title-card-alquiler {
    font-family: "radicalis";
    font-weight: 400;
    color: $white;
    font-size: 2rem;
  }
  .text-card-alquiler {
    color: $white;
  }
}
.section-2 {
  padding-top: 3rem;
  padding-bottom: 3rem;
  background-color: $gray-claro;
  a {
    color: $gray-siroco;
    text-decoration: none;
    word-break: break-all;
    transition: all 0.3s;
    span {
      font-weight: 800;
    }
    &:hover {
      color: $blue-siroco;
    }
  }
}
